<template>
  <div class="login-container">
    <el-card class="login-card">
      <h2>Áî®Êà∑ÁôªÂΩï</h2>
      <el-form
        ref="loginForm"
        :model="form"
        :rules="rules"
        label-width="80px"
        class="login-form"
      >
        <el-form-item label="ÈÇÆÁÆ±" prop="email">
          <el-input v-model="form.email" type="email" placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"></el-input>
        </el-form-item>

        <el-form-item label="ÂØÜÁ†Å" prop="password">
          <el-input v-model="form.password" type="password" placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleLogin" :loading="loading">
            ÁôªÂΩï
          </el-button>
          <el-button @click="$router.push('/register')">Ê≥®ÂÜåË¥¶Âè∑</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import axios from 'axios'

export default {
  name: 'Login',
  setup() {
    const router = useRouter()
    const loginForm = ref(null)
    const loading = ref(false)

    const form = reactive({
      email: '',
      password: ''
    })

    const rules = {
      email: [
        { required: true, message: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±', trigger: 'blur' },
        { type: 'email', message: 'ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÈÇÆÁÆ±Ê†ºÂºè', trigger: 'blur' }
      ],
      password: [
        { required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur' }
      ]
    }

    const handleLogin = async () => {
      if (!loginForm.value) return

      await loginForm.value.validate(async (valid) => {
        if (valid) {
          loading.value = true
          try {
            const response = await axios.post('/api/auth/login', form)
            if (response.data.status === 200) {
              const { token } = response.data.data
              localStorage.setItem('token', token)
              ElMessage.success('ÁôªÂΩïÊàêÂäü')
              router.push('/')
            }
          } catch (error) {
            const message = error.response?.data?.message || 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
            ElMessage.error(message)
          } finally {
            loading.value = false
          }
        }
      })
    }

    return {
      loginForm,
      form,
      rules,
      loading,
      handleLogin
    }
  }
}
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f5f7fa;
}

.login-card {
  width: 480px;
}

.login-form {
  margin-top: 20px;
}
</style>
