<template>
  <div class="register-container">
    <el-card class="register-card">
      <h2>Áî®Êà∑Ê≥®ÂÜå</h2>
      <el-form
        ref="registerForm"
        :model="form"
        :rules="rules"
        label-width="80px"
        class="register-form"
      >
        <el-form-item label="ÈÇÆÁÆ±" prop="email">
          <el-input v-model="form.email" type="email" placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"></el-input>
        </el-form-item>

        <el-form-item label="Áî®Êà∑Âêç" prop="username">
          <el-input v-model="form.username" placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"></el-input>
        </el-form-item>

        <el-form-item label="ÂØÜÁ†Å" prop="password">
          <el-input v-model="form.password" type="password" placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"></el-input>
        </el-form-item>

        <el-form-item label="È™åËØÅÁ†Å" prop="code" v-if="showVerificationCode">
          <el-input v-model="form.code" placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å">
            <template #append>
              <el-button
                :disabled="countdown > 0"
                @click="handleSendCode"
              >
                {{ countdown > 0 ? `${countdown}ÁßíÂêéÈáçËØï` : 'Ëé∑ÂèñÈ™åËØÅÁ†Å' }}
              </el-button>
            </template>
          </el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleRegister" :loading="loading">
            Ê≥®ÂÜå
          </el-button>
          <el-button @click="$router.push('/login')">ËøîÂõûÁôªÂΩï</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import axios from 'axios'

export default {
  name: 'Register',
  setup() {
    const router = useRouter()
    const registerForm = ref(null)
    const loading = ref(false)
    const showVerificationCode = ref(false)
    const countdown = ref(0)

    const form = reactive({
      email: '',
      username: '',
      password: '',
      code: ''
    })

    const rules = {
      email: [
        { required: true, message: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±', trigger: 'blur' },
        { type: 'email', message: 'ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÈÇÆÁÆ±Ê†ºÂºè', trigger: 'blur' }
      ],
      username: [
        { required: true, message: 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç', trigger: 'blur' },
        { min: 3, max: 20, message: 'Áî®Êà∑ÂêçÈïøÂ∫¶Âú®3Âà∞20‰∏™Â≠óÁ¨¶‰πãÈó¥', trigger: 'blur' }
      ],
      password: [
        { required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur' },
        { min: 8, message: 'ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë‰∏∫8‰∏™Â≠óÁ¨¶', trigger: 'blur' }
      ],
      code: [
        { required: true, message: 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å', trigger: 'blur' },
        { len: 6, message: 'È™åËØÅÁ†ÅÈïøÂ∫¶‰∏∫6‰Ωç', trigger: 'blur' }
      ]
    }

    const startCountdown = () => {
      countdown.value = 60
      const timer = setInterval(() => {
        countdown.value--
        if (countdown.value <= 0) {
          clearInterval(timer)
        }
      }, 1000)
    }

    const handleSendCode = async () => {
      try {
        const response = await axios.post('/api/auth/resend-verification', {
          email: form.email
        })
        if (response.data.status === 200) {
          startCountdown()
          showVerificationCode.value = true
          ElMessage.success('È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÔºåËØ∑Êü•Êî∂ÈÇÆ‰ª∂')
        }
      } catch (error) {
        ElMessage.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
      }
    }

    const handleRegister = async () => {
      if (!registerForm.value) return

      await registerForm.value.validate(async (valid) => {
        if (valid) {
          loading.value = true
          try {
            const response = await axios.post('/api/auth/register', form)
            if (response.data.status === 201) {
              router.push('/register-success')
            }
          } catch (error) {
            const message = error.response?.data?.message || 'Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
            ElMessage.error(message)
          } finally {
            loading.value = false
          }
        }
      })
    }

    return {
      registerForm,
      form,
      rules,
      loading,
      showVerificationCode,
      countdown,
      handleSendCode,
      handleRegister
    }
  }
}
</script>

<style scoped>
.register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f5f7fa;
}

.register-card {
  width: 480px;
}

.register-form {
  margin-top: 20px;
}
</style>
